import { ADD_ONE } from './constants'

const initialState = {
    counter: 1
}

export const homePageReducer = (state = initialState, action:any) => {
    console.log('reducer', action.type)
	switch(action.type) {
	case ADD_ONE:
		return {
			...state, 
			counter: state.counter += action.payload
		}
		break
	default: 
		return state
	}
}